  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: calc(100% - 30px);
  background: rgba(176, 196, 222, 0.42);
  /* background: rgba(0,255,255, 0.5); */
  /* linear-gradient(45deg, , rgb(176, 196, 222, 0.8)), */
  transition: all .3s linear;
  margin-left: 15px;
  /* padding-right: 15px; */
  z-index: 1007;
}
